const config = {
    defaultContentElement: null,
    defaultIconPack: 'mdi',
    defaultTooltipType: 'is-primary',
    defaultTooltipAnimated: false,
    defaultInputAutocomplete: 'on'
}

export default config

export const setOptions = options => { this.config = options }



